import React, { Component } from "react";
import { Widget, addResponseMessage } from "react-chat-widget";
import "react-chat-widget/lib/styles.css";
import "./chatbot.css";
import * as tf from "@tensorflow/tfjs";
import * as use from "@tensorflow-models/universal-sentence-encoder";
import id_to_embedding_mapping from "../../chatbotdata/id_to_embedding_mapping.json";
import embedding_matrix from "../../chatbotdata/embedding_matrix.json";
import chatbot_intents from "../../chatbotdata/chatbot_intents.json";
import chatbot_config from "../../chatbotdata/chatbot_config.json";

class ChatbotService {
  constructor() {
    // Initialize the model and other data here
    this.model = null;
    this.embedMatrix = null;
    this.intents = chatbot_intents;
    this.id_to_embedding_mapping = id_to_embedding_mapping;
  }

  async init() {
    // Load the Universal Sentence Encoder model
    try {
      this.model = await use.load();
      console.log("Universal Sentence Encoder loaded successfully");
    } catch (error) {
      console.error("Error loading Universal Sentence Encoder:", error);
      process.exit(1);
    }

    // Load other data, e.g., embedding_matrix, chatbot_intents
    this.embedMatrix = tf.tensor(await embedding_matrix["matrix"]);
    // const embedMatrixnorm=tf.norm(this.embedMatrix,2,0)
    // this.embedMatrix= tf.div(this.embedMatrix,embedMatrixnorm)

    // const numRows = this.embedMatrix.shape[0];
    // const normalizedMatrix = [];

    // for (let i = 0; i < numRows; i++) {
    //   const row =this. embedMatrix.slice([i, 0], [1, this.embedMatrix.shape[1]]);
    //   const normalizedRow = tf.div(row, tf.norm(row));
    //   normalizedMatrix.push(normalizedRow);
    // }
    // this.embedMatrix =tf.concat(normalizedMatrix);

    // You can load chatbot_intents here if needed

    console.log("ChatbotService initialized");
  }

  async classifyIntent(message) {
    // Check if the model is loaded before using it
    if (this.model) {
      // Encode the user's message using the loaded model
      const encoding = await this.model.embed([message]);

      // Compare the encoded message to known intents
      const intent = await this.findBestIntent(encoding);
      if (intent === "-1") {
        // Return the corresponding response or a clarifying message if no intent is found
        //console.log(this.intents[chatbot_config["default_id"]])

        const res = this.intents[chatbot_config["default_id"]]["response"];
        return res[Math.floor(Math.random() * res.length)];
      } else {
        const indentId = this.id_to_embedding_mapping[intent];
        const res2 = this.intents[indentId]["response"];

        return (
          res2[Math.floor(Math.random() * res2.length)] ||
          `I'm not sure what you're asking. Can you please clarify your question?`
        );
      }
    } else {
      console.error("Model not loaded. Cannot classify intent.");
      return "Model not loaded. Cannot classify intent. wait for few seconds...";
    }
  }

  findBestIntent(encoding) {
    const encodingNorm = tf.norm(encoding, 2, 1);
    const encodingUnit = tf.div(encoding, encodingNorm);

    const cosineSimilarities = this.embedMatrix.dot(encodingUnit.transpose());

    const argMax = cosineSimilarities.argMax().dataSync();
    const maxIndex = argMax[0];
    const score = cosineSimilarities.dataSync()[maxIndex];
    console.log(score);
    if (score > chatbot_config["intent_threshold"]) return maxIndex;

    return "-1";
  }
}

class Chatbot extends Component {
  constructor(props) {
    super(props);
    this.chatbotService = new ChatbotService();
  }

  async componentDidMount() {
    // Initialize ChatbotService, which loads the model and data
    await this.chatbotService.init();
  }

  handleNewUserMessage = async (newMessage) => {
    const response = await this.chatbotService.classifyIntent(newMessage);
    console.log(`Response: ${response}`);
    addResponseMessage(response);
  };

  render() {
    return (
      <Widget
        width={200}
        height={200}
        handleNewUserMessage={this.handleNewUserMessage}
        profileAvatar="bot.png"
        title="Ravi's Personal AI Chatbot 🤖"
        profileClientAvatar="recruit.png"
        subtitle="Engage, Explore, and Discover My Work"
      />
    );
  }
}

export default Chatbot;
